












import { Vue, Component, Prop } from 'vue-property-decorator';

@Component({})
export default class ToolboxCreateItem extends Vue {
    //////////////////// prop ////////////////////

    @Prop({
        type: Object,
        default: () => {
            return {};
        },
    })
    private parameter: object;

    //////////////////// Vue Life ////////////////////

    private async beforeCreate() {}
    private async created() {}
    private async beforeMount() {}
    private async mounted() {}
    private async beforeDestroy() {}
    private async destroyed() {}

    //////////////////// View Control ////////////////////

    //////////////////// Private Function ////////////////////

    private handleClick() {
        this.$emit('click-create', JSON.parse(JSON.stringify(this.parameter)));
    }
}
Vue.component('toolbox-create-item', ToolboxCreateItem);
